.react-calendar-heatmap .color-empty { fill: #eee; }
.react-calendar-heatmap .color-scale-1 { fill: #d6e685; }
.react-calendar-heatmap .color-scale-2 { fill: #a6d075; }
.react-calendar-heatmap .color-scale-3 { fill: #8cc665; }
.react-calendar-heatmap .color-scale-4 { fill: #6cb653; }
.react-calendar-heatmap .color-scale-5 { fill: #44a340; }
.react-calendar-heatmap .color-scale-6 { fill: #228336; }
.react-calendar-heatmap .color-scale-7 { fill: #1e6823; }

.nonscrollable, .result, .dates {
    overflow: hidden;
}

.scrollable {
    overflow: auto;
}

.result, .dates {
    position: absolute;
    left: 0;
    right: 0;
}

.result {
    top: 0;
    height: 40%;
}

.dates {
    bottom: 0;
    height: 60%;
}

.border {
    position: absolute;
    left: 10px;
    top: 10px;
    right: 10px;
    bottom: 10px;
}

textarea {
    width: 100%;
    height: 100%;
}

pre.error {
    color: red;
}



